import { MyPosts, MyProblems, Notices } from "../actionTypes";
const initialState = 0;

const HomeReducer = (state = initialState, action) => {
  switch (action.type) {
    case MyPosts:
      return 0;
    case MyProblems:
      return 1;
    case Notices:
      return 2;
    default:
      return state;
  }
};

export default HomeReducer;
