import React from "react";
import { Responsive } from "semantic-ui-react";
import ForComp from "./forComp/forComp";
import ForTab from "./forTablet/forTab";
import { connect } from "react-redux";
import ForMob from "./forMob/forMob";

const MyProblems = props => {
  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <ForComp></ForComp>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ForTab></ForTab>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <ForMob></ForMob>
      </Responsive>
    </div>
  );
};

export default connect()(MyProblems);
