import React, { useState } from "react";
import {
  Backdrop,
  Zoom,
  Slide,
  Button,
  Snackbar,
  IconButton
} from "@material-ui/core";
import { Modal, Form, TextArea } from "semantic-ui-react";
import { Edit, PhotoCamera, CloudOutlined } from "@material-ui/icons";
import { postMyPost, postPhoto } from "../../../ApiRequests/request";
import { Upload, message } from "antd";
import url from "../../../strings/url";
const token = localStorage.getItem("AnonymityToken#123");
const PostProblem = props => {
  const [isLoading, setLoading] = useState(false);
  const [status, setStatus] = useState("unknown");
  const [description, setDescription] = useState("");
  const [isUploaded, setUploaded] = useState(false);
  const [problemImageUrl, setUrl] = useState(null);

  return (
    <div>
      {status === "error" ? (
        <Snackbar
          open={status !== "unknown"}
          message="Error Posting"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}
      {status === "success" ? (
        <Snackbar
          open={status !== "unknown"}
          message="SuccessFul"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}
      <Modal open={props.open} dimmer="blurring" onClose={props.onClose}>
        <Modal.Header>
          Post Problem<Edit></Edit>
        </Modal.Header>
        <Modal.Content>
          <Form loading={isLoading}>
            <TextArea
              value={description}
              onChange={e => {
                setDescription(e.target.value);
              }}
              placeholder="Describe Your Problem !! And Description Must Contain At least 10 Characters"
            ></TextArea>
          </Form>
        </Modal.Content>
        <Modal.Content>
          <Upload
            method="PUT"
            accept="image/*"
            headers={{
              Authorization: token,
              contentType: "application/json"
            }}
            name="image"
            action={`${url}/user/post/photo`}
            onChange={info => {
              if (info.file.status === "done") {
                console.log(info.file.response);
                setUploaded(true);
                setLoading(false);
                setUrl(info.file.response);
              } else if (info.file.status === "removed") {
                setLoading(false);
                setUploaded(false);
              } else if (info.file.status === "uploading") {
                setLoading(true);
              }
            }}
            onRemove={() => {
              console.log("on Remove called");
              const headers = new Headers();
              headers.append("Authorization", token);
              headers.append("content-type", "application/json");
              const request = new Request(
                `${url}/post/photo/${problemImageUrl.key}`,
                { method: "DELETE", headers }
              );
              fetch(request)
                .then(res => {
                  console.log(res.status);
                })
                .catch(e => {
                  console.log(e);
                });
              setUrl(null);
            }}
            disabled={isUploaded}
          >
            <Button
              style={{
                fontFamily: "poppins",
                fontWeight: "bold",
                textTransform: "none"
              }}
              disabled={isUploaded}
            >
              <div style={{ marginRight: 10 }}>Upload Photo</div>
              <PhotoCamera
                style={{ color: "rgba(44,130,201,1)" }}
              ></PhotoCamera>
            </Button>
          </Upload>
        </Modal.Content>
        <Modal.Actions>
          <Button
            color="secondary"
            size="medium"
            style={{
              marginBottom: 10,
              color: "white",
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins"
            }}
            disabled={description.length < 10 || isLoading}
            onClick={() => {
              setLoading(true);
              const token = localStorage.getItem("AnonymityToken#123");
              postMyPost(token, description, "problem", problemImageUrl)
                .then(res => {
                  if (res.status === 200) {
                    window.location.reload(false);
                    setStatus("success");
                    props.onClose();
                    return;
                  }
                  setLoading(false);
                  setStatus("error");
                  props.onClose();
                })
                .catch(err => {
                  setLoading(false);
                  setStatus("error");
                });

              setUrl(null);
            }}
            variant="contained"
            disableElevation
          >
            Post Problem
          </Button>
          <Button
            size="medium"
            style={{
              marginBottom: 10,
              color: "white",
              background: "rgba(240,0,0,1)",
              textTransform: "none",
              fontFamily: "poppins"
            }}
            onClick={() => {
              props.onClose();
              if (problemImageUrl) {
                const headers = new Headers();
                headers.append("Authorization", token);
                headers.append("content-type", "application/json");
                const request = new Request(
                  `${url}/post/photo/${problemImageUrl.key}`,
                  { method: "DELETE", headers }
                );
                fetch(request)
                  .then(res => {
                    console.log(res.status);
                    setUrl(null);
                  })
                  .catch(e => {
                    setUrl(null);
                    console.log(e);
                  });
              }
              setUploaded(false);
            }}
            variant="contained"
            disableElevation
          >
            Close
          </Button>
        </Modal.Actions>
      </Modal>
    </div>
  );
};

export default PostProblem;
