import React from "react";
import { connect } from "react-redux";
import {
  Tabs,
  Tab,
  Paper,
  createMuiTheme,
  ThemeProvider
} from "@material-ui/core";
import { orange, blue } from "@material-ui/core/colors";
import {
  NotificationsActiveOutlined,
  ChatBubbleOutlineOutlined,
  SentimentDissatisfiedOutlined
} from "@material-ui/icons";
// import { BackTop } from "antd";
const theme = createMuiTheme({
  palette: {
    primary: blue,
    secondary: orange
  }
});
class ForComp extends React.Component {
  componentDidMount() {
    if (this.props.location.pathname.toLowerCase().includes("myposts"))
      this.props.dispatch({ type: "MyPosts" });
    else if (this.props.location.pathname.toLowerCase().includes("myproblems"))
      this.props.dispatch({ type: "MyProblems" });
    else if (this.props.location.pathname.toLowerCase().includes("notices"))
      this.props.dispatch({ type: "Notices" });
    else {
      this.props.dispatch({ type: "MyPosts" });
      this.props.history.push("/anonymity/home/myposts");
    }
  }
  render() {
    return (
      <ThemeProvider theme={theme}>
        <Paper elevation={0}>
          <Tabs
            indicatorColor="secondary"
            onChange={(event, value) => {
              if (value == 0) {
                this.props.history.push("/anonymity/Home/MyPosts");
                this.props.dispatch({ type: "MyPosts" });
              } else if (value == 1) {
                this.props.history.push("/anonymity/Home/MyProblems");
                this.props.dispatch({ type: "MyProblems" });
              } else if (value == 2) {
                this.props.history.push("/anonymity/Home/Notices");
                this.props.dispatch({ type: "Notices" });
              }
            }}
            centered
            textColor="secondary"
            value={this.props.currentHomeTab}
          >
            <Tab
              // style={{ marginRight: "2%" }}
              icon={<ChatBubbleOutlineOutlined></ChatBubbleOutlineOutlined>}
              label="My Posts"
            ></Tab>
            <Tab
              // style={{ marginRight: "2%" }}
              icon={
                <SentimentDissatisfiedOutlined></SentimentDissatisfiedOutlined>
              }
              label="My Problems"
            ></Tab>
            <Tab
              icon={<NotificationsActiveOutlined></NotificationsActiveOutlined>}
              label="Notices"
            ></Tab>
          </Tabs>
        </Paper>
      </ThemeProvider>
    );
  }
}

const mapStateToProps = state => {
  return {
    currentHomeTab: state.HomeReducer
  };
};

export default connect(mapStateToProps)(ForComp);
